import Rete from "rete";
// import { TextControl } from "../Controls/TextControl";
import { NumControl } from "../Controls/NumControl";
// import { strSocket } from "../Sockets/Sockets";
import { numSocket } from "../Sockets/Sockets";
import { MyNode } from "./MyNode";

export class MidjorneyNode extends Rete.Component {
  constructor() {
    super("Midjorney");
    this.data.component = MyNode; // optional
  }

  builder(node) {
    let inp1 = new Rete.Input("num1", "Number", numSocket);
    let out1 = new Rete.Output("num", "Number", numSocket);

    inp1.addControl(new NumControl(this.editor, "num1", node));

    return node
      .addInput(inp1)
      .addOutput(out1)
      .addControl(new NumControl(this.editor, "preview", node, true));
  }

  worker(node, inputs, outputs) {
    let n1 = inputs["num1"].length ? inputs["num1"][0] : node.data.num1;

    this.editor.nodes
      .find((n) => n.id === node.id)
      .controls.get("preview")
      .setValue(n1);
    outputs["num"] = n1;
  }
}

