import React from "react";
import { createEditor, editor } from "../rete";
import Sidebar from "./Sidebar";
import Header from "./Header";

export default function MainLayout() {
  let node = "";

  const greateNodeHandler = (el) => {
    node = el;
  };

  const dragOverHandler = (el) => {
    el.preventDefault();

    // console.log("over");
    // console.log(el);
    // engine.abort();
  };

  const dropHandler = async (el) => {
    el.preventDefault();
    el.persist();

    const { x, y, k } = await editor.view.area.transform;
    const localX = el.clientX - el.target.offsetLeft;
    const localY = el.clientY - el.target.offsetTop;
    const position = [(localX - x) / k, (localY - y) / k];

    let component = await node.createNode();
    component.position = position;
    editor.addNode(component);
  };

  return (
    <div className="layout">
      <Header />
      <Sidebar handler={greateNodeHandler} />
      <div
        className="layout__content"
        // style={{ width: "100vw", height: "100vh" }}
        ref={(ref) => ref && createEditor(ref)}
        onDragOver={dragOverHandler}
        onDrop={dropHandler}
      />
    </div>
  );
}
